import React from 'react';
import { cva } from 'class-variance-authority';
import { clsx } from 'clsx';
import { GlobalVariants } from '../../interfaces/global-variants';
import { HeaderShortComponentProps, HeaderShortProps, HeaderShortVariants } from './headershort.interface';

export const headerShortHeadlineVariants = {
  [HeaderShortVariants.ZpsBg]: ['zep-text-typography-light-100'],
  [HeaderShortVariants.CatBg]: ['zep-text-typography-light-100'],
  [GlobalVariants.Zps]: ['zep-text-indigo-500'],
  [GlobalVariants.Cat]: ['zep-text-typography-dark-100'],
};

export const headerShortTaglineVariants = {
  [GlobalVariants.Zps]: ['zep-text-typography-brand-steel'],
  [GlobalVariants.Cat]: ['zep-text-typography-light-70 zep-opacity-70'],
  [HeaderShortVariants.CatBg]: ['zep-text-typography-light-70 zep-opacity-70'],
  [HeaderShortVariants.ZpsBg]: ['zep-text-typography-light-70 zep-opacity-70'],
};

export const headlineClass = cva([`zep-typography-headlineLG-fluid-cqi zep-break-all`], {
  variants: {
    variant: headerShortHeadlineVariants,
  },
  defaultVariants: {
    variant: GlobalVariants.Zps,
  },
});

export const taglineClass = cva(['zep-mb-0.5 zep-typography-taglineMD zep-break-all',], {
  variants: {
    variant: headerShortTaglineVariants,
  },
  defaultVariants: {
    variant: GlobalVariants.Zps,
  },
});

export const HeaderShortComponent = ({ tagline, headline, showArrow, className, variant = GlobalVariants.Zps }: HeaderShortComponentProps) => {
  return (
    <div
      className={clsx(
        'zep-flex',
        'zep-items-center',
        'xl:zep-gap-3.5',
        'md:zep-gap-1.5',
        'sm:zep-gap-2.5',
        'zep-gap-1.5',
        className,
      )}
      data-testid="header-short-component"
    >
      {showArrow ? (
        <img
          className={clsx(
            'zep-h-4',
            'sm:zep-h-5',
            'md:zep-h-4',
            'xl:zep-h-[100px]',
            'zep-w-auto',
          )}
          alt="forward arrow"
          src="./assets/moving-forward-arrow.svg"
        />
      ) : null}
      <div className="zep-w-full">
        {tagline ? (
          <h3
            className={clsx(
              taglineClass({ variant }),
              'zep-mb-0.5',
              'zep-typography-taglineMD',
              'zep-break-all',
            )}
          >
            {tagline}
          </h3>
        ) : null}
        {headline ? (
          <h2 className={headlineClass({ variant })}>
            {headline}
          </h2>
        ) : null}
      </div>
    </div>
  )
}

export const HeaderShort: React.FC<HeaderShortProps> = ({ className, variant = GlobalVariants.Zps, ...props }) => {
  const colSpanByArrow = props.showArrow ? 'md:zep-col-span-6' : 'md:zep-col-span-5';
  const backgroundColor = {
    'zep-bg-background-gradient': variant === HeaderShortVariants.ZpsBg,
    'zep-bg-background-dark': variant === HeaderShortVariants.CatBg,
  }

  const paddingByVariant = variant === HeaderShortVariants.CatBg || variant === HeaderShortVariants.ZpsBg
    ? 'md:zep-py-5 sm:zep-py-4 zep-py-3'
    : ''

  return (
    <div
      className={clsx(
        'zep-container',
        'zep-grid',
        'zep-gap-1',
        'sm:zep-gap-1.5',
        'md:zep-gap-2',
        'zep-grid-cols-4',
        'sm:zep-grid-cols-8',
        'md:zep-grid-cols-16',
        backgroundColor,
        paddingByVariant,
        className,
      )}
      data-testid="header-short"
    >
      <HeaderShortComponent
        {...props}
        variant={variant}
        className={clsx(
          'zep-col-span-4',
          'sm:zep-col-span-8',
          'md:zep-col-start-2',
          'zep-px-1',
          'sm:zep-px-1.5',
          'md:zep-px-[0px]',
          colSpanByArrow,
        )}
      />
    </div>
  );
};
